import classNames from "classnames";
import Button from "../../../../components/Button/Button";
import Icon from "../../../../components/Icon/Icon";
import UnderlineText from "../../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import '../Position/card.less';
import dateFormat from "date-format";
export function Card({ history, lang, wallet }) {

  return (
    <div className="position-card">
      <div className="market-close-header">
        <div className="market-name">
          <div className="symbol-name">
            {/* <Icon token={history.underlier} width="16" height="16" /> */}
            <span>{history.symbol}</span>
          </div>
          <div className={classNames("leverage-direction", { "dir-long": history.direction === "Long", "liqtion": history.direction === "LIQUIDATION" })}>
            {history.direction}
          </div>
        </div>
      </div>
      <div className="position-card-info-box">
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['time']}
          </div>
          <div className="position-card-info-value">
            {dateFormat.asString("yyyy-MM-dd hh:mm:ss", new Date(parseInt(history.time)))}&nbsp;&nbsp;
            <a target="_blank" rel="noreferrer" href={`${wallet.blockExploreUrl}/tx/${history.transactionHash}`}>
              <Icon token="scan" width="14" />
            </a>
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            <UnderlineText tip={lang["entry-price-tip"]}>
              {lang['entry-price']}
            </UnderlineText>
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={history.price} decimalScale={2} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            <UnderlineText tip={lang["index-price-tip"]}>
              {lang['index-price']}
            </UnderlineText>
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={history.indexPrice} decimalScale={2} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['volume']}
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={history.volume} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['notional']}
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={history.notional} decimalScale={2} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['transaction-fee']}
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={history.transactionFee} decimalScale={2} />
          </div>
        </div>
      </div>
    </div>
  )
}