import Tab from "../../../../components/Tab/Tab"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat"
import Search from "../../../../components/Search/Search"
import './symbolPanel.less'
import { inject, observer } from "mobx-react"
import classNames from 'classnames'
import Icon from "../../../../components/Icon/Icon"
import { useMemo, useState, useEffect } from "react"
import { useHistory } from 'react-router-dom'
import Category from "../../../../model/Category";
import UnderlineText from '../../../../components/UnderlineText/UnderlineText'
import { buried } from "../../../../utils/utils"
import { isArbiChain, isBSCChain } from "../../../../lib/web3js/v3/config"
import { GAMMA, POWER } from "../../../../utils/Constants"


function SymbolPanel({ trading, lang, onClose, onChanged }) {
  const [filterSymbols, setFilterSymbols] = useState([])
  const [symbols, setSymbols] = useState([])
  const [isSearch, setIsSearch] = useState(false);
  const history = useHistory()

  const onSelect = (info, force) => {
    if (trading.symbolInfo.displaySymbol !== info.displaySymbol || trading.symbolInfo.bTokenSymbol !== info.bTokenSymbol || force) {
      onChanged && onChanged(true)
      const prefix = trading.pool.retired || info.retired ? '/retired' : ''
      let url = `${prefix}/trade/${info.displayCategory.toLowerCase()}/${info.displaySymbol}`
      if ((info.isV2 || info.singleBtoken)) {
        url = `${url}/${info.bTokenSymbol}`
      }
      buried("symbol_select", "symbol_select", info.displaySymbol)
      history.push(url)
      trading.setCookie(info.symbol, info.category, info.bTokenSymbol)
    }
    onClose && onClose()
  }

  const filter = (list, keyword) => {
    setFilterSymbols(list)
    if (keyword) {
      setIsSearch(true)
    } else {
      setIsSearch(false)
    }
  }

  const onFocus = () => {
    setIsSearch(true)
  }

  const onBlur = e => {
    setIsSearch(false)
  }

  useEffect(() => {
    setSymbols(trading.symbolInfos)
    setFilterSymbols(trading.symbolInfos)
  }, [trading.symbolInfos])


  return (
    <div className='symbol-panel'>
      <div className='sp-main'>
        <Search list={filterSymbols} width='326' onFocus={onFocus} onBlur={onBlur} placeholder='Search Options, Futures and Powers' height='32' searchName='displaySymbol' onChange={(list, keyword) => filter(list, keyword)} className='symbol-search' />
        {
          isSearch
            ?
            <SearchPanel symbols={filterSymbols} all={symbols} lang={lang} onSelect={onSelect} />
            :
            <TabPanel symbols={filterSymbols} lang={lang} onSelect={onSelect} current={Category.current} chainId={trading.wallet && trading.wallet.chainId} />
        }
      </div>
    </div>
  )
}

function SearchPanel({ symbols = [], all = [], lang, onSelect }) {
  const [groupd, setGroupd] = useState({})

  const hotClick = symbol => {
    const symbolInfo = all.find(s => s.displaySymbol === symbol)
    if (symbolInfo) {
      onSelect(symbolInfo, true)
    }
  }

  useEffect(() => {
    if (symbols && symbols.length >= 0) {
      const groupedSymbols = symbols.reduce((total, item) => {
        if (total[item.displayCategory]) {
          total[item.displayCategory].push(item)
        } else {
          total[item.displayCategory] = [item]
        }
        return total;
      }, {})
      setGroupd(groupedSymbols)
    }
  }, [symbols])
  return (
    <div className='search-panel'>
      <div>
        <div className='hot-symbol'>
          <span className='hot-symbol-item' onClick={() => hotClick('BTCUSD-40000-C')}>
            BTCUSD-40000-C  &nbsp; <Icon token='hot' width='10' /><Icon token='hot' width='10' />
          </span>
          <span className='hot-symbol-item' onClick={() => hotClick('mBTC^2')}>
            mBTC^2 &nbsp;<Icon token='hot' width='10' />
          </span>
          <span className='hot-symbol-item' onClick={() => hotClick('mETH^2')}>
            mETH^2  &nbsp;<Icon token='hot' width='10' />
          </span>
        </div>
      </div>
      <div className='search-list'>
        {Object.keys(groupd).map((key, index) => {
          return (
            <div key={index}>
              <div className='category'>{key}</div>
              <div className='symbol-list'>
                <SymbolList symbols={groupd[key]} onSelect={onSelect} lang={lang} zone={groupd[key].zone} category={key} />
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

function TabPanel({ symbols = [], lang, onSelect, current, chainId }) {
  const [activeIndex, setActiveIndex] = useState('0');
  const zones = { all: 'all' }
  const optionsCond = { all: 'all', main: 'BTC', inno: 'ETH' }
  if (+chainId === 56) {
    optionsCond['inno2'] = 'BNB'
  }
  const tabSet = useMemo(() => {
    symbols.forEach(s => !zones[s.zone] && s.zone && (zones[s.zoneDisplay] = s.zoneDisplay))
    symbols.some(symbol => symbol.deriBased) && Object.assign(optionsCond, { 'DERI-Based': 'DERI-Based' })    
    const data = {
      [lang['futures']]: {
        content: <SymbolListWithFilter lang={lang} symbols={symbols} searchFields={['zoneDisplay']} conditions={zones} category='futures' onSelect={onSelect} />,
        className: 'symbol-item'
      },
      [lang['options']]: {
        content: <SymbolListWithFilter lang={lang} symbols={symbols} searchFields={['symbol', 'zoneDisplay']} conditions={optionsCond} category='option' onSelect={onSelect} />,
        className: 'symbol-item'
      }
    }
    if(symbols.some(symbol => symbol.category === POWER)) {
      data[lang['powers']] = {
        content: <SymbolListWithFilter lang={lang} symbols={symbols} searchFields={['symbol']} conditions={{ all: 'all', main: 'BTC', inno: 'ETH' }} category='power' onSelect={onSelect} />,
        className: 'symbol-item'
      }
    }
    if(symbols.some(symbol => symbol.category === GAMMA)) {
      data[lang['gamma']] = {
        content: <SymbolListWithFilter lang={lang} symbols={symbols} searchFields={['symbol']} conditions={{ all: 'all', btc: 'BTC', eth: 'ETH'}} category='gamma' onSelect={onSelect} />,
        className: 'symbol-item',
        isBet: true
      }
    }
    if (isBSCChain(String(chainId))) {
      data[[lang['gamma']]] = {
        content: <SymbolListWithFilter lang={lang} symbols={symbols} searchFields={['symbol']} conditions={{ all: 'all', btc: 'BTC', eth: 'ETH',bnb : 'BNB' }} category='gamma' onSelect={onSelect} />,
        className: 'symbol-item',
        isBet: true
      }
    }

    return data
  }, [lang, symbols])

  useEffect(() => {
    if (current === 'option') {
      setActiveIndex(1)
    } else if (current === 'power') {
      setActiveIndex(2)
    } else if (current === 'gamma') {
      setActiveIndex(3)
    } else {
      setActiveIndex(0)
    }
  }, [current])

  return <Tab tabSet={tabSet} className='tab-panel' activeIndex={activeIndex} />
}

function SymbolListWithFilter({ symbols, lang, conditions, category, onSelect, searchFields }) {

  const tabSet = useMemo(() => {
    return Object.keys(conditions).reduce((total, key) => {
      const filterSymbols = conditions[key] === 'all' ? symbols : symbols.filter(s => searchFields.some(searchField => s[searchField].match(new RegExp(`${conditions[key]}`))))
      const label = lang[conditions[key]] || conditions[key]
      return {
        ...total,
        [label]: {
          content: <SymbolList lang={lang} symbols={filterSymbols} key={key} category={category} onSelect={onSelect} />,
          className: conditions[key] === "BNB" ? 'filter-item filter-item-bnb' : 'filter-item'
        }
      }
    }, {})
  }, [symbols, category, lang])

  return <Tab tabSet={tabSet} lang={lang} symbols={symbols} className='filter-tab' />
}

function SymbolList({ symbols, version, onSelect, lang, zone, category }) {
  const [filterSymbols, setFilterSymbols] = useState([])

  useEffect(() => {
    let list = symbols.filter(s => s.displayCategory === category.toUpperCase()).sort((s1, s2) => s1.version > s2.version ? -1 : 0).sort((s1, s2) => {
      if ((s1.zone === 'lite' || s1.zone === 'inno') && s2.zone === 'main') {
        return 1
      } else if (s1.zone === 'main' && (s2.zone === 'lite' || s2.zone === 'inno')) {
        return -1
      } else {
        return 0
      }
    })
    if (zone) {
      list = list.filter(s => s.zone.indexOf(zone) > -1)
    }
    if (category === 'option') {
      list = list.sort((s1, s2) => {
        if (/^bnb/i.test(s2.symbol) && !/^bnb/i.test(s1.symbol)) {
          return 1
        } else if (/^bnb/i.test(s1.symbol) && !/^bnb/i.test(s2.symbol)) {
          return -1
        } else {
          return 0
        }
      })
    }
    setFilterSymbols(list)
  }, [symbols, zone, category])

  return (
    <div className='symbols'>
      <div className='s-body'>
        {filterSymbols.map((info, index) => {
          const volume24Class = classNames('volume24', { up: info.volume24h > 0, down: info.volume24h < 0 })
          return (<div className='symbol-row' key={index} onClick={() => onSelect(info)}>
            <div className='ss-name'>
              <Icon token={info.symbol} secondary={info.underlier} height={20} width={20} />
              {info.formattedUnit === "m" ? <>
                <div className="m-symbol-box">
                  {info.formatedSymbol}
                  <div className="m-tip">
                    {info.formatedSymbol}  represents 1/1000 of {info.symbol}
                  </div>
                </div>
              </> : info.formatedSymbol}
              {info.new && <Icon token='new-symbol' width={30} height={16} className='new-label' />}
              {info.deriBased && <UnderlineText className='deri-based-tip' tip={`Trade ${info.symbol} with ${info.bTokenSymbol} as base token.`}  ><Icon width='16' token='deri-based-tip' /></UnderlineText>}
            </div>
            {!info.isGamma && <div className='ss-value'>
              <div className='mark-price'><DeriNumberFormat prefix='$' value={info.markPrice} decimalScale={2} /></div>
              <div className='volume24-changed'>
                <div className='direction'>{info.volume24h && !isNaN(info.volume24h) && <Icon width={8} token={info.volume24h > 0 ? 'rate-up' : 'rate-down'} />}</div>
                <div className={volume24Class}><DeriNumberFormat value={isNaN(info.volume24h) ? info.volume24h : info.volume24h * 100} decimalScale={2} suffix='%' /></div>
              </div>
            </div>}
          </div>

          )
        })}
      </div>
    </div>
  )
}


export default inject('trading')(observer(SymbolPanel))