import { hide } from 'react-functional-modal'
import hint from '../Announcement/img/hint.svg'
import './riskWarning.less'
import { setCookie } from '../../utils/utils'
export default function ZksyncRiskWarning() {
  const closeRisk = () => {
    hide("ZksyncRiskWarning")
    setCookie("ZksyncRiskWarning-modal", "false")
  }
  return (
    <div className="risk-warning">
      <div className="risk-header">
        <div className='risk-img'>
          <img src={hint} alt='' />
          Risks Related to Derivatives Trading on Zksync Era
        </div>
      </div>
      <div className="risk-box">
        Please note that Deri is currently in its beta version on ZkSync Era. Before engaging, it is important to understand the associated risks. Derivatives trading may result in a total loss of invested funds. Therefore, it is essential to use the platform at your own risk and exercise caution. Deri Protocol is not liable for any losses incurred while using the platform.
      </div>
      <div className="risk-close">
        <div className="risk-close-btn" onClick={closeRisk}>
          OK,  I Understand
        </div>
      </div>
    </div>
  )
}