import { useEffect, useState, useRef } from 'react'
import './symbolInfo.less'
import DeriNumberFormat from '../../../../utils/DeriNumberFormat'
import { firstLetterUppercase, isMobile } from '../../../../utils/utils'
import Icon from '../../../../components/Icon/Icon'
import classNames from 'classnames'
import SymbolPanel from './SymbolPanel'
import { inject, observer } from 'mobx-react'
import Category from '../../../../model/Category'

function SymbolInfo({ trading, onGirdLayoutChange, lang }) {
  const [isShow, setIsShow] = useState(false)
  const [isKlineOn, setIsKlineOn] = useState(false)
  const [dynamicKey, setDynamicKey] = useState(new Date().getTime())
  const [symbolChanging, setSymbolChanging] = useState(true);
  const [isKlineHidden, setIsKlineHidden] = useState(false)

  const rateClass = classNames('rate', {
    'up': trading.tradeInfo.volume24h > 0,
    'down': trading.tradeInfo.volume24h < 0
  })
  const symbolMarketClass = classNames('symbol-market', {
    show: isShow
  })

  const klineDispalyClass = classNames('kline-icon', {
    hidden: isKlineHidden
  })

  const symbolInfoClass = classNames('symbol-info', {
    gamma: Category.isGamma
  })

  const onLayoutChange = (isMobile) => {
    setIsKlineOn(isMobile)
    onGirdLayoutChange(isMobile)
  }
  //just for mobile responsive
  const closeKline = () => {
    onLayoutChange(false)
  }
  const onClose = (e) => {
    if (!e || canClose(e)) {
      setIsShow(false)
    }
  }

  const onSymbolChanged = () => {
    setSymbolChanging(true)
  }

  const canClose = (e) => {
    const selectAreaElement = document.querySelector('.s-left')
    const searchElement = document.querySelector('.symbol-panel')
    return !hasParent(selectAreaElement, e.target) && !hasParent(searchElement, e.target)
  }

  const hasParent = (parent, current) => {
    if (parent === current) {
      return true
    } else if (current.parentElement) {
      return hasParent(parent, current.parentElement)
    }
    return false
  }

  const onShow = () => {
    const show = !isShow
    setIsShow(show)
    show && setDynamicKey(new Date().getTime())
  }

  useEffect(() => {
    window.addEventListener('resize', closeKline)
    window.addEventListener('click', onClose)
    setSymbolChanging(true)
    if (trading.currentSymbol.symbol) {
      setSymbolChanging(false)
    }
    return () => {
      window.removeEventListener('resize', closeKline)
      window.removeEventListener('click', onClose)
    }
  }, [trading.currentSymbol, trading.tradeInfo.markPrice])

  useEffect(() => {
    const onScroll = e => {
      if (document.body.scrollTop > 64 || document.documentElement.scrollTop > 64) {
        setIsKlineHidden(true)
      } else {
        setIsKlineHidden(false)
      }
    }
    window.addEventListener('scroll', onScroll)
    return () => window.removeEventListener('scroll', onScroll)
  }, []);
  return (
    <div className={symbolInfoClass} >
      <div className='main' >
        <div className='s-left' onClick={onShow}>
          <div className='market-area'>
            <div className={classNames('zone', trading.currentSymbol.zoneDisplay, { 'loading-line': symbolChanging })}>
              {!symbolChanging && trading.currentSymbol.zoneDisplay && firstLetterUppercase(trading.currentSymbol.zoneDisplay)}
            </div>
            <div className={classNames('symbol-name', { 'loading-line': symbolChanging })}>
              {!symbolChanging && <><Icon token={trading.currentSymbol.symbol} secondary={trading.currentSymbol.underlier} width={30} height={30} />{trading.currentSymbol.displaySymbol}</>}
              <div className={classNames('select-icon', { "gamma-select-icon": Category.isGamma })}>
                <Icon token={isShow ? 'arrow-up' : 'arrow-down'} alt='select' />
              </div>
            </div>
          </div>
          <div className={classNames('select-icon', { "gamma-select-icon": Category.isGamma })}>
            <Icon token={isShow ? 'arrow-up' : 'arrow-down'} alt='select' />
          </div>
          <div className='divider-line'></div>
        </div>
        <div className='index-area'>
          <div className='index-area-wraper'>
            <div className={rateClass}>
              {trading.tradeInfo.volume24h && !isNaN(trading.tradeInfo.volume24h) && <Icon token={trading.tradeInfo.volume24h > 0 ? 'rate-up' : 'rate-down'} alt='' />}
              <DeriNumberFormat value={isNaN(trading.tradeInfo.volume24h) ? trading.tradeInfo.volume24h : trading.tradeInfo.volume24h * 100} decimalScale={2} suffix='%' />
            </div>
            <div className='index-value'><DeriNumberFormat value={trading.tradeInfo.markPrice} decimalScale={trading.currentSymbol.decimals} /></div>
          </div>
        </div>
        <div className={klineDispalyClass} onClick={() => onLayoutChange(!isKlineOn)}>
          {Category.isGamma ?
            <div className='simulator-open'>Simulator</div>
            : null}
          <Icon token={isKlineOn ? 'close-kline' : Category.isGamma ? 'calculator' : 'kline'} width={isKlineOn ? '16' : '24'} height={isKlineOn ? '16' : '24'} />
        </div>
      </div>
      <div className={symbolMarketClass} key={dynamicKey}>
        <SymbolPanel lang={lang} onClose={onClose} onChanged={onSymbolChanged} />
      </div>
    </div>
  )
}
export default inject('trading')(observer(SymbolInfo))