import React, { useRef, useEffect, useState } from 'react'
import styled from 'styled-components'
import { widget } from '../../../lib/charting_library'
import { getFormatSymbol, intervalRange, bg, countDecimal, isMobile, } from '../../../utils/utils';
import axios from 'axios';
import { inject, observer } from 'mobx-react';
import tvConfig from './tv-config.json'
import WebSocket from '../../../model/WebSocket';
import Type from '../../../model/Type';
import Version from '../../../model/Version';
import { SHORT_NAME_MAP } from '../../../utils/Constants';
import GammaPnlSimulate from '../../../components/GammaPnlSimulate/GammaPnlSimulate'
import { useLocation } from 'react-router';
// axios.defaults.withCredentials = true

const supported_resolutions = ["1", "5", "15", "30", "60", "4H", "1D", "5D", "1W", "1M"];
const GET_KLINE_URL = `${process.env.REACT_APP_KLINE_HTTP_URL}/kline`
let symbolInfo = {}
const subscribes = {}
const webSocket = new WebSocket(process.env.REACT_APP_KLINE_WSS_URL)

const Wrapper = styled.div`

  @media only screen and (min-width: 375px) {
    height : 100%;
    min-height:400px;
  }

  @media only screen and (min-width: 1140px) {
    height : 100%;
  }
 
  @media only screen and (min-width: 1568px) {
    height : 100%;
  }
  @media only screen and (min-width: 2560px) {
    height : 100%;
  }
  position: relative;
  #tv-container {
    height : 100%;
  }
  .loading {
    width: 56px;
    height: 56px;
    top: calc( 50% - 28px);
    left: calc( 50% - 28px);
    border-width : 3px;
  }
`

function Tradingview({ trading, intl, interval = '60', type, wallet, lang }) {
  const widgetRef = useRef(null);
  const containerRef = useRef(null)
  const lastDataRef = useRef(null);
  const subscribeIdRef = useRef(null);
  const historyRef = useRef(null)
  const [loading, setLoading] = useState(true)
  const location = useLocation();
  const isGammas = location.pathname.split('/')[2] === "gamma"
  // const cur
  const datafeedRef = useRef({
    onReady: (callback) => {
      setTimeout(() => callback({ supported_resolutions: supported_resolutions }), 0);
    },
    getBars: (symbolInfo, resolution, from, to, onHistoryCallback, onErrorCallback, firstDataRequest) => getBars(symbolInfo, resolution, from, to, onHistoryCallback, onErrorCallback, firstDataRequest),
    resolveSymbol: (symbolName, onSymbolResolvedCallback) => resolveSymbol(symbolName, onSymbolResolvedCallback),
    subscribeBars: (symbolInfo, resolution, onRealtimeCallback, subscribeUID, onResetCacheNeededCallback) => subscribeBars(symbolInfo, resolution, onRealtimeCallback, subscribeUID, onResetCacheNeededCallback),
    unsubscribeBars: subscriberUID => unsubscribeBars(subscriberUID)
  });

  const klineBarDataMap = (d) => {
    const unit = SHORT_NAME_MAP[symbolInfo.formattedUnit]
    if (symbolInfo.isPower && unit && /mark/i.test(d.id)) {
      d.open = bg(d.open).times(unit).toNumber()
      d.close = bg(d.close).times(unit).toNumber()
      d.high = bg(d.high).times(unit).toNumber()
      d.low = bg(d.low).times(unit).toNumber()
    }
    return d
  }

  function klineDataProcess(data) {
    if (symbolInfo.isPower) {
      return data.map(klineBarDataMap)
    }
    return data;
  }


  const getBars = async (symbolInfo, resolution, from, to, onHistoryCallback, onErrorCallback, firstDataRequest) => {
    const target = symbolInfo.target
    const type = symbolInfo.type
    const params = {
      symbol: target.symbol,
      chain: target.chain,
      type: type,
      pool: target.address,
      period: intervalRange[resolution],
      from: from,
      to: to
    }
    var url = new URL(GET_KLINE_URL)
    Object.keys(params).forEach(key => url.searchParams.append(key, params[key]))
    const res = await fetch(url).catch(e => console.error(e) && initialize())
    if (res && res.ok) {
      const data = await res.json();
      if (data.length > 0) {
        const klineData = klineDataProcess(data)
        onHistoryCallback(klineData)
        lastDataRef.current = klineData[klineData.length - 1]
        historyRef.current = klineData
      } else {
        onHistoryCallback([], { noData: true })
      }
    }
  }

  const subscribeBars = (symbolInfo, resolution, onRealtimeCallback, subscribeUID, onResetCacheNeededCallback) => {
    const target = symbolInfo.target
    const type = symbolInfo.type
    const params = {
      symbol: target.symbol, type: type, pool: target.address,
      chain: target.chain, period: intervalRange[resolution]
    }
    subscribeIdRef.current = subscribeUID
    webSocket.subscribe('update', params, data => {
      subscribes[subscribeUID] = params
      if (data && lastDataRef.current && data.time >= lastDataRef.current.time) {
        const mapData = klineBarDataMap(Object.assign({}, data))
        onRealtimeCallback(mapData)
        lastDataRef.current = mapData
      }
    })
  }

  const unsubscribeBars = subscriptUID => {
    if (subscribes[subscriptUID]) {
      webSocket.unsubscribe('unsubscribe_update', subscribes[subscriptUID])
    }
  }

  const resolveSymbol = (symbol, onSymbolResolvedCallback) => {
    let description = symbol;
    if (symbolInfo.isFuture && !Version.isOpen) {
      description = `${symbol}-MARK`
    }
    const type = /index/i.test(symbol) ? 'index' : 'mark'
    setTimeout(() => onSymbolResolvedCallback({
      name: symbol,
      ticker: symbol,
      description: description,
      pricescale: symbol.indexOf('-INDEX') > 0 ? 100 : 1 * (10 ** symbolInfo.decimals),
      target: symbolInfo,
      type: type,
      minmov: 1,
      has_intraday: true,
      intraday_multipliers: ["1", "2", "5", "15", "30", "60", "4H", "1D", "7D", "1W", "1M"],
      has_weekly_and_monthly: true,
      has_no_volume: true,
      has_daily: true,
      timezone: 'UTC',
      session: "24x7",
      supported_resolutions: supported_resolutions
    }), 0)
  }

  const initialize = () => {
    const timezone = Intl ? Intl.DateTimeFormat().resolvedOptions().timeZone : 'Etc/UTC'
    const { overrides, ...rest } = tvConfig
    const widgetOptions = {
      symbol: symbolInfo.displaySymbol,
      datafeed: datafeedRef.current,
      interval: interval,
      locale: intl.locale.replace('-', '_'),
      timezone: timezone,
      ...rest
    }
    if (isMobile()) {
      widgetOptions["enabled_features"] = ["hide_left_toolbar_by_default"]
    }
    widgetRef.current = new widget(widgetOptions);
    widgetRef.current.onChartReady(() => {
      setLoading(false)
      widgetRef.current.applyOverrides(overrides)
      const priceFormatter = widgetRef.current.chart().priceFormatter();
      priceFormatter._formatAsDecimal = (d) => {
        const decimals = symbolInfo.getDecimalBySymbol(symbolInfo.symbol)
        const price = bg(d).toFixed(decimals, 1)
        return price
      }

      if (!Version.isOpen) {
        const priceScale = symbolInfo.isFuture ? 'as-series' : 'new-left'
        let underline = `${symbolInfo.symbol.split('-')[0].replace(/\^2/, '')}-INDEX`;
        widgetRef.current.chart().createStudy('Overlay', true, false, [underline], null, { priceScale: priceScale })
        if (symbolInfo.isFuture) {
          widgetRef.current.chart().createStudy('Moving Average', false, false, [7])
          widgetRef.current.chart().createStudy('Moving Average', false, false, [25])
          widgetRef.current.chart().createStudy('Moving Average', false, false, [99])
        }
      }
    })
  }

  useEffect(() => {
    setLoading(true)
    // return () => webSocket.disconnect()
  }, [trading.symbolInfo.symbol])


  useEffect(() => {
    if (trading.symbolInfo && trading.symbolInfo.displaySymbol && !widgetRef.current && !isGammas) {
      symbolInfo = trading.symbolInfo
      initialize();
      webSocket.onRecconnect(() => {
        //如果当前数据不是最新或者没有历史数据，重新加载
        const curTimestamp = new Date().getTime();
        if (containerRef.current && (historyRef.current === null || (lastDataRef.current && lastDataRef.current.time && curTimestamp > lastDataRef.current.time))) {
          initialize();
        }
      })
    }
    return () => {
      if (widgetRef.current && trading.symbolInfo.displaySymbol !== symbolInfo.displaySymbol) {
        widgetRef.current.remove();
        widgetRef.current = historyRef.current = lastDataRef.current = null;
      }
      if (subscribeIdRef.current) {
        webSocket.unsubscribe(subscribes[subscribeIdRef.current])
      }
    }
  }, [trading.symbolInfo, intl.locale, wallet.detail.chainId])

  useEffect(() => {
  }, [])

  return (
    <Wrapper>
      {isGammas ? <GammaPnlSimulate lang={lang} /> : <>
        {loading && <div className='loading'></div>}
        <div id='tv-container' ref={containerRef} />
      </>}

    </Wrapper>
  )

}

export default inject('trading', 'intl', 'type', 'wallet')(observer(Tradingview))