import { useEffect, useState, useCallback, useRef } from "react";
import { inject, observer } from "mobx-react";
import "./gammaPnlSimulate.less";
import classNames from "classnames";
import Icon from "../Icon/Icon";
import ApiProxy from '../../model/ApiProxy'
import Chart from "./Chart";
import { show } from "react-functional-modal";
import DeriNumberFormat from "../../utils/DeriNumberFormat";
import { SHORT_NAME_MAP } from "../../utils/Constants";
import GammaModal from "../GammaModal/GammaModal";
import UnderlineText from "../UnderlineText/UnderlineText";
function GammaPnlSimulate({ trading, lang, wallet }) {
  const [isMyPosition, setIsMyPosition] = useState(false);
  const [openCondition, setOpenCondition] = useState(false);
  const [isTabSymbol, setIsTabSymbol] = useState(false);
  const [indexValue, setIndexValue] = useState();
  const [funding, setFunding] = useState();
  const [volume, setVolume] = useState(0.01);
  const [nowInfo, setNowInfo] = useState({});
  const [isLong, setIsLong] = useState(true);
  const [fundingDays, setFundingDays] = useState(1);
  const [lastData, setLastData] = useState({});
  const [apply, setApply] = useState(false)
  const [data, setData] = useState(null)
  const [initData, setInitData] = useState(null)
  const currentRef = useRef()
  const currentPositionRef = useRef()
  const showModal = () => {
    show(<GammaModal />, {
      key: "gammaModal",
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 2,
      },
    });
  };
  const onChangeIndex = useCallback((e) => {
    let value = e.target.value;
    if (value < 0) {
      return;
    }
    setIndexValue(value);
    setApply(false)
  }, []);
  const onChangeVolume = useCallback((e) => {
    let value = e.target.value;
    setVolume(value);
    setApply(false);
  }, []);
  const onChangeFunding = useCallback((e) => {
    let value = e.target.value;
    setFundingDays(value);
  }, []);
  const onChangeChart = (props) => {
    if (props) {
      setNowInfo(props[0].payload)
    }
  }
  const reset = () => {
    setVolume(0.01)
    setIndexValue("")
    setApply(false)
    currentRef.current = false
    setIsLong(true)
  }
  const load = useCallback(async () => {
    if (trading && trading.tradeInfo && trading.curTradeUnit && trading.tradeInfo.indexPrice) {
      let price = apply && indexValue ? indexValue : trading.tradeInfo.indexPrice
      let entryVolume = apply && volume ? volume : 0.01;
      if (isMyPosition && trading.position.displayVolume !== "0") {
        entryVolume = trading.position.displayVolume
        price = trading.position.gammaEntryPrice
      }
      if (!isLong && !isMyPosition) {
        entryVolume = -entryVolume
      }
      let curIndexPrice = trading.tradeInfo.indexPrice
      let res = await getGammaSimulatePnls(trading, price, curIndexPrice, entryVolume, isMyPosition)
      if (res) {
        res = res.map((item) => {
          item.curIndexPrice = Number(item.curIndexPrice)
          item.entryPrice = Number(item.entryPrice)
          item.pnl = Number(item.pnl);
          return item
        })
        if (res.length > 0) {
          let data = res.find((item) => Number(curIndexPrice) === item.curIndexPrice)
          if (data) {
            setLastData(data)
            setNowInfo(data)
          }

        }
        if (!(!isMyPosition && currentPositionRef.current)) {
          setData(res)
        }
        setInitData(res)
      }
      if (!res[0] || !res[0].pnl) {
        currentRef.current = false
      }
      if ((isMyPosition && trading.position.displayVolume !== "0") && !(res[0] || !res[0].pnl)) {
        currentPositionRef.current = false
      }
    }

  }, [trading, indexValue, apply, volume, isLong, isMyPosition, trading.position.displayVolume])
  const tabLong = useCallback((bool) => {
    if (bool === isLong) {
      return false;
    }
    if (data && initData) {
      let arr = JSON.parse(JSON.stringify(initData))
      arr = arr.map((item) => {
        item.pnl = -(item.pnl)
        item.gamma = -(item.gamma)
        item.detla = -(item.detla)
        return item
      })
      if (arr.length > 0) {
        let index = 0;
        let n = arr.length;
        if (n % 2 === 0) {
          index = n / 2
        } else {
          index = (n - 1) / 2
        }
        setLastData(arr[index])
        setNowInfo(arr[index])
      }
      setData(arr)
      setIsLong(bool)
      setInitData(arr)
      setData(arr)
      setIsLong(bool)
    }
  }, [initData, data, isLong])

  useEffect(() => {
    if (trading.curTradeUnit && trading.currentSymbol.displaySymbol) {
      setVolume(0.01)
      setIndexValue("")
      setApply(false)
      setIsTabSymbol(!isTabSymbol)
      currentRef.current = false
      currentPositionRef.current = false
    }
  }, [trading.curTradeUnit, trading.currentSymbol.displaySymbol])

  useEffect(() => {
    let time = 1000 * 60 * 3;
    let timer = window.setInterval(() => {
      if (currentRef.current) {
        currentRef.current = false
        currentPositionRef.current = false
      }
    }, time)
    return () => {
      timer && clearInterval(timer)
    }
  }, [])

  useEffect(() => {
    if (trading.position.displayVolume && trading.position.displayVolume !== "0") {
      if (+trading.position.displayVolume > 0) {
        setIsLong(true)
      } else if (+trading.position.displayVolume < 0) {
        setIsLong(false)
      }
      setIsMyPosition(true)
    } else {
      setIsMyPosition(false)
    }
  }, [trading.position.displayVolume])

  useEffect(() => {
    if (!isMyPosition && trading && trading.tradeInfo && trading.tradeInfo.indexPrice && trading.currentSymbol && !currentRef.current) {
      load()
      currentRef.current = true
    } else if (!currentPositionRef.current && isMyPosition && trading.tradeInfo && trading.tradeInfo.indexPrice && trading.currentSymbol) {
      load()
      currentPositionRef.current = true
    }
  }, [trading.tradeInfo, isTabSymbol, trading.tradeInfo.indexPrice, trading.currentSymbol.displaySymbol, trading, apply, volume, trading.position.displaySymbol, trading.position.displayVolume, isMyPosition, isLong])

  useEffect(() => {
    if (trading.tradeInfo.funding0 && fundingDays) {
      let entryVolume = volume
      let fundingFee = trading.tradeInfo.funding0 * fundingDays * entryVolume
      if (isMyPosition && trading.position.displayVolume !== "0") {
        fundingFee = trading.tradeInfo.funding0 * fundingDays * Math.abs(trading.position.displayVolume)
      }
      if (isLong) {
        fundingFee = fundingFee * -1
      }
      setFunding(fundingFee)
    } else if (fundingDays === 0) {
      setFunding(0)
    }
  }, [trading.tradeInfo.funding0, fundingDays, volume, isLong, isMyPosition])
  return (
    <div className="gamma-pnl-simulate">
      <div className={classNames("gamma-pnl-simulate-header", { "openCondition-header": openCondition })}>
        <div className="pnl-simulate-tab">
          <div
            className={classNames("simulate-btn", { active: !isMyPosition })}
            onClick={() => { currentRef.current = false; currentPositionRef.current = false; setIsMyPosition(false) }}
          >
            {lang["pnl-simulator"]}
          </div>
          <div
            className={classNames("simulate-btn", { active: isMyPosition })}
            onClick={() => {
              setIsMyPosition(true); if (+trading.position.displayVolume > 0) {
                setIsLong(true)
              } else if (+trading.position.displayVolume < 0) {
                setIsLong(false)
              }
            }}
          >
            {lang["my-position"]}
          </div>
        </div>
        {isMyPosition ? null : <>
          <div className="advanced-settings-box  pc-settings">
            {openCondition ? (
              <div className="condition-entry ">
                <div className="entry-price-box">
                  <div className="entry-price-title">{lang["entry-price"]}</div>
                  <div className="check-use-index-entry-price">
                    <div
                      className="input-index-price"
                    >
                      <input
                        type="number"
                        className="input-price"
                        value={indexValue}
                        onChange={onChangeIndex}
                        placeholder={`Current ${trading.symbolInfo.gammaUnderlier} Price`}
                      />
                    </div>
                  </div>
                </div>
                <div className="entry-volume-box">
                  <div className="volume-price-title">{lang["volume"]}</div>
                  <div className="input-entry-volume">
                    <input
                      type="number"
                      className="input-volume"
                      value={volume}
                      onChange={onChangeVolume}
                    />
                    <div>{trading.curTradeUnit}</div>
                  </div>
                </div>
                <div className="apply-condition" onClick={() => { currentRef.current = false; setApply(true) }}>
                  Apply
                </div>
                <div className="reset-condition" onClick={() => reset()}>
                  <Icon
                    token="reset"
                  />
                  Reset
                </div>
              </div>
            ) :
              <div
                className="open-modal-gamma-btn"
                onClick={showModal}
              >
                What is Gamma Swap?
              </div>
            }
            <div
              className="advanced-settings-btn"
              onClick={() => setOpenCondition(!openCondition)}
            >
              <Icon
                token={openCondition ? "right" : "left"}
              />
              Advanced Settings
            </div>
          </div>
          {openCondition ? null : <div
            className="open-modal-gamma-btn m-open-modal-gamma-btn"
            onClick={showModal}
          >
            What is Gamma Swap?
          </div>}
          <div className={classNames("advanced-settings-box m-settings", { "m-no-openCondition": !openCondition })}>
            <div className="advnced-settings-btn"
              onClick={() => setOpenCondition(!openCondition)}
            >
              Advanced Settings
              <Icon token={openCondition ? 'arrow-up' : 'arrow-down'} width='20' height='20' />
            </div>
            {openCondition ? <div className="condition-entry ">
              <div className="entry-price-box">
                <div className="entry-price-title">{lang["entry-price"]}</div>
                <div className="check-use-index-entry-price">

                  <div
                    className="input-index-price"
                  >
                    <input
                      type="number"
                      className="input-price"
                      value={indexValue}
                      onChange={onChangeIndex}
                      placeholder={`Current ${trading.symbolInfo.gammaUnderlier} Price`}
                    />

                  </div>
                </div>
              </div>
              <div className="volume-apply-reset-box">
                <div className="entry-volume-box">
                  <div className="volume-price-title">{lang["volume"]}</div>
                  <div className="input-entry-volume">
                    <input
                      type="number"
                      className="input-volume"
                      value={volume}
                      onChange={onChangeVolume}
                    />
                    <div>{trading.curTradeUnit}</div>
                  </div>
                </div>
                <div className="apply-condition" onClick={() => { currentRef.current = false; setApply(true); }}>
                  Apply
                </div>
                <div className="reset-condition" onClick={() => reset()}>
                  <Icon
                    token="reset"
                  />
                  Reset
                </div>
              </div>
            </div> : null}
          </div>
        </>}

      </div>
      <div className={classNames("gamma-pnl-simulate-chart-info", { "openCondition-chart-info": openCondition })}>
        {(trading.position.displayVolume === "0" || !trading.positions.length) && isMyPosition ? (
          <div className="chart-mask">
            <Icon
              token="chart-mask"
            />
            No {trading.currentSymbol.displaySymbol}  position found.
          </div>
        ) : null}
        <div className="pnl-chart">
          <div className="max">Pnl </div>
          <div className="long-short">
            {isMyPosition ? <>
              <div className={classNames("my-position-direction", {
                "long-direction": trading.position.direction === "Long",
                "short-direction": trading.position.direction === "Short"
              })}>
                {trading.position.direction && trading.position.direction.toLocaleUpperCase()}
                <span>
                  {trading.curTradeUnit}
                </span>
              </div>
            </> : <>
              <div
                className={isLong ? "long check" : "long"}
                onClick={() => {
                  tabLong(true);
                }}
              >
                <Icon
                  token="long-pnl"
                />
                Long
              </div>
              <div
                className={isLong ? "short" : "short check"}
                onClick={() => {
                  tabLong(false);
                }}
              >
                <Icon
                  token="short-pnl"
                />
                Short
              </div></>}
          </div>
          <div className="chart-pnl-box">
            {!data && <div className='loading-box'>
              <div className='loading'></div>
            </div>}
            <Chart
              arr={data}
              onChange={onChangeChart}
              lastData={lastData}
              isLong={isLong}
              underlier={trading.symbolInfo.gammaUnderlier}
            />
          </div>

        </div>
        <div className="info-change">
          <div className="entry-price-change">
            {/* {lang["index-price"]} */}
            {lastData.curIndexPrice !== nowInfo.curIndexPrice ?
              `${trading.currentSymbol.gammaUnderlier} Price`
              : `Current ${trading.currentSymbol.gammaUnderlier} Price`
            }
            <span>
              <span className="chart-index-price"> <DeriNumberFormat value={lastData.curIndexPrice} decimalScale={2} /></span> {lastData.curIndexPrice !== nowInfo.curIndexPrice ? <>
                {`->`} <DeriNumberFormat value={nowInfo.curIndexPrice} decimalScale={2} /></> : null}
            </span>
          </div>
          <div className="other-info">
            <div className="other-info-title">
              <div className="greeks-box">Greeks</div>
              <div className="payout-box">Payout</div>
              <div className="margin-box">Margin</div>
            </div>
            <div className="other-info-box">
              <div className="other-info-box-detail">
                <div className="other-info-box-detail-title">Gamma</div>
                <div className="other-info-box-detail-num">
                  <DeriNumberFormat value={lastData.gamma} decimalScale={6} /> {trading.currentSymbol.gammaUnderlier}
                </div>
              </div>
              <div className="other-info-box-detail pnl-detail">
                <div className="other-info-box-detail-title">
                  <UnderlineText tip="Calculated based on the index price, assuming no slippage when closing the position.">
                    Pnl
                  </UnderlineText>
                </div>
                <div className="other-info-box-detail-num">
                  <DeriNumberFormat value={lastData.pnl} decimalScale={2} prefix="$" />
                  {lastData.pnl !== nowInfo.pnl ? <>
                    {`->`}  <DeriNumberFormat value={nowInfo.pnl} decimalScale={2} prefix="$" /></> : null}</div>
              </div>
              <div className="other-info-box-detail maintainence-initial">
                <div className="other-info-box-detail-title">
                  Initial Margin
                </div>
                <div className="other-info-box-detail-num">
                  <DeriNumberFormat value={lastData.initialMargin} decimalScale={2} prefix="$" />
                  {lastData.initialMargin !== nowInfo.initialMargin ? <>
                    {`->`}  <DeriNumberFormat value={nowInfo.initialMargin} decimalScale={2} prefix="$" /></> : null}
                </div>
              </div>
              <div className="other-info-box-detail">
                <div className="other-info-box-detail-title">Delta</div>
                <div className="other-info-box-detail-num">
                  <DeriNumberFormat value={lastData.detla} decimalScale={2} />
                  {lastData.detla !== nowInfo.detla ? <>
                    {`->`} <DeriNumberFormat value={nowInfo.detla} decimalScale={2} /> </> : null} {trading.currentSymbol.gammaUnderlier}
                </div>
              </div>
              <div className="other-info-box-detail funding-simulate">
                <div>
                  <div className="other-info-box-detail-title">Avg Funding</div>
                  <div className="other-info-box-detail-num">
                    <DeriNumberFormat value={funding} decimalScale="4" prefix="$" />
                  </div>
                </div>
                <div>
                  <div className="other-info-box-detail-title">
                    Holding Period
                  </div>
                  <div className="other-info-box-detail-num">
                    <div className="funding-days">
                      <input
                        type="number"
                        value={fundingDays}
                        onChange={onChangeFunding}
                      />
                    </div>
                    <div className="funding-days-add-remove">
                      <Icon
                        token="funding-add"
                        onClick={() => {
                          let days = fundingDays + 1;
                          setFundingDays(days);
                        }}
                      />
                      <Icon
                        token="funding-remove"
                        className="funding-remove-btn"
                        onClick={() => {
                          let days = fundingDays - 1;
                          if (days >= 0) {
                            setFundingDays(days);
                          }
                        }}
                      />
                    </div>
                    <div className="funding-days-title">Days</div>
                  </div>
                </div>
              </div>
              <div className="other-info-box-detail maintainence-initial">
                <div className="other-info-box-detail-title">
                  Maintenance Margin
                </div>
                <div className="other-info-box-detail-num">
                  <DeriNumberFormat value={lastData.maintenanceMargin} decimalScale={2} prefix="$" />
                  {lastData.maintenanceMargin !== nowInfo.maintenanceMargin ? <>
                    {`->`} <DeriNumberFormat value={nowInfo.maintenanceMargin} prefix="$" decimalScale={2} /></> : null}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

const getGammaSimulatePnls = async (trading, entryPrice, curIndexPrice, volume = 0.01, isMyPosition) => {
  entryPrice = entryPrice ? entryPrice : trading.tradeInfo.indexPrice
  let multiplier = trading.symbolInfo.formattedUnit ? SHORT_NAME_MAP[trading.symbolInfo.formattedUnit] : 1
  volume = volume * multiplier
  let params = [trading.currentSymbol.chainId, trading.currentSymbol.address, trading.currentSymbol.symbol, entryPrice, curIndexPrice, volume, !isMyPosition]
  console.log("params", params)
  let res = await ApiProxy.request("calculateSimulateForGamma", params)
  return res.map(item => {
    item.entryPrice = entryPrice
    return item
  })
};

export default inject('trading', 'wallet')(observer(GammaPnlSimulate))
