  //     averageEntryPrice: {
  //       text: lang["entry-price"],
  //       value: (pos) =>
  //         pos.isGamma
  //           ? <span>
  //             <UnderlineText tip={getLang([pos.direction === "Long" ? "gamma-enter-price-value-hover" : "gamma-enter-price-value-hover-short"], {
  //               unit: pos["symbolUnit"], price: pos["gammaEntryPrice"] && pos["gammaEntryPrice"].toFixed(2)
  //             })}>
  //               {formatNumber(
  //                 pos["gammaEntryPrice"],
  //                 trading.symbolInfo.getDecimalBySymbol &&
  //                 trading.symbolInfo.getDecimalBySymbol(pos.symbol)
  //               )}
  //             </UnderlineText>
  //           </span>
  //           : formatNumber(pos["averageEntryPrice"], 2),
  //     },
  //     markPrice: {
  //       text: lang["mark-price"], value: (pos) => pos.isGamma
  //         ? "--"
