import React, { useState, useEffect } from "react";
import styled from "styled-components";
import UnderlineText from "../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";
import { inject, observer } from "mobx-react";
import { useLocation } from "react-router-dom";
import Type from "../../../model/Type";
import Category from "../../../model/Category";
import Intl from "../../../model/Intl";
import classNames from "classnames";
import "./tradeInfo.less";
import Icon from "../../../components/Icon/Icon";
import { show } from "react-functional-modal";
import SimulatePnl from "../../../components/SimulatePnl/SimulatePnl";
import { buried, isMobile } from "../../../utils/utils";
const getLang = (key) => {
  return Intl.get("trade", key);
};

function TradeInfo({ trading, lang, wallet, getLangKey }) {
  const [columns, setColumns] = useState([]);
  const location = useLocation();
  let isRetired = location.pathname.split("/")[1] === "retired" ? true : false;
  const fileds = {
    "mark-price": {
      text: getLang("mark-price"),
      value: "",
      decimal: trading.symbolInfo.decimals,
      type: "number",
    },
    "index-price": {
      text:
        trading.symbolInfo.isPower || trading.symbolInfo.isGamma
          ? `${trading.symbolInfo.underlying} ${getLang("price")}`
          : getLang("index-price"),
      value: "",
      decimal: 2,
      type: "number",
    },
    dvol: { text: getLang("dvol"), tip: getLang("dvol-hover"), value: "", suffix: "%", decimal: 2 },
    "daily-funding": { text: getLang("daily-funding"), value: "", suffix: "%", decimal: 2 },
    "24h-change": { text: getLang("24h-change"), value: "", type: "number", suffix: "%" },
    "total-net-position": {
      text: getLang("total-net-position"),
      tip: getLang("total-net-position-hover"),
      value: "",
      decimal: 2,
      type: "number",
    },
    "margin-req": { text: getLang("margin-req"), value: "", tip: getLang("margin-req-hover") },
    fee: { text: getLang("fee"), value: "" },
    payoff: { text: getLang("payoff"), value: "", tip: getLang("payoff-hover") },
    "up-depth": { text: getLang("up-depth"), value: "", tip: getLang("up-depth-hover") },
    "power-price": {
      text: trading.symbolInfo.underlying && trading.symbolInfo.underlying + getLang("power-price"),
      value: "",
      decimal: trading.symbolInfo.decimals,
    },
  };
  const showSimulatePnl = (wallet, trading, lang) => {
    buried("pnl_simulator", "simulator_view", "pnl_simulator");
    show(<SimulatePnl wallet={wallet} lang={lang} trading={trading} />, {
      key: "simulatePnl",
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0.4)",
        zIndex: 99,
      },
    });
  };
  useEffect(() => {
    let cols = [];
    if (Category.isFutures) {
      cols = [
        { ...fileds["mark-price"], value: trading.tradeInfo.markPrice },
        { ...fileds["index-price"], value: trading.tradeInfo.indexPrice },
        {
          ...fileds["daily-funding"],
          value: () => {
            return (
              <>
                <UnderlineText tip={trading.fundingFeeValueTip} multiline className="funding-rate">
                  <DeriNumberFormat value={`${trading.tradeInfo.funding0}`} decimalScale="4" />
                </UnderlineText>
                &nbsp;&nbsp;
                <UnderlineText tip={trading.rateTip} multiline className="funding-rate">
                  (
                  <DeriNumberFormat
                    suffix="%"
                    decimalScale="4"
                    value={trading.tradeInfo.fundingFeeRate * 100}
                  />
                  )
                </UnderlineText>
              </>
            );
          },
          tip: trading.fundingFeeTip,
        },
        {
          ...fileds["24h-change"],
          value: trading.tradeInfo.volume24h * 100,
          className: trading.tradeInfo.volume24h
            ? trading.tradeInfo.volume24h > 0
              ? "up calculator"
              : "down calculator"
            : "calculator",
          icon: isRetired ? (
            ""
          ) : (
            <Icon token="calculator" onClick={() => showSimulatePnl(wallet, trading, lang)} />
          ),
          iconTip: isRetired ? "" : lang["simulate-pnl-icon-tip"],
        },
        {
          ...fileds["total-net-position"],
          value: () => (
            <DeriNumberFormat
              value={trading.tradeInfo.tradersNetVolume}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
        {
          ...fileds["up-depth"],
          value: () => (
            <DeriNumberFormat
              decimalScale="2"
              value={trading.tradeInfo.impactUpNotional}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
      ];
      if (trading.symbolInfo.isV2) {
        cols.pop();
      }
    } else if (Category.isPower) {
      cols = [
        {
          ...fileds["mark-price"],
          value: trading.tradeInfo.markPrice,
          tip: trading.powerMarkPriceTip,
        },
        {
          ...fileds["index-price"],
          value: () => (
            <>
              <DeriNumberFormat value={trading.tradeInfo.indexPrice} decimalScale={2} /> | DVOL :{" "}
              <DeriNumberFormat
                value={trading.tradeInfo.volatility * 100}
                decimalScale={2}
                suffix="%"
              />
            </>
          ),
        },
        { ...fileds["power-price"], value: trading.tradeInfo.powerPrice },
        {
          ...fileds["daily-funding"],
          value: () => {
            return (
              <div>
                <UnderlineText tip={trading.fundingFeeValueTip} multiline className="funding-rate">
                  <DeriNumberFormat value={`${trading.tradeInfo.funding0}`} decimalScale="2" />
                </UnderlineText>
                &nbsp;&nbsp;
                <UnderlineText tip={trading.rateTip} multiline className="funding-rate">
                  (
                  <DeriNumberFormat
                    suffix="%"
                    decimalScale="4"
                    value={trading.tradeInfo.fundingFeeRate * 100}
                  />
                  )
                </UnderlineText>
              </div>
            );
          },
          tip: trading.fundingFeeTip,
        },
        {
          ...fileds["24h-change"],
          value: () => (
            <>
              {" "}
              <span
                className={
                  trading.tradeInfo.volume24h && (trading.tradeInfo.volume24h > 0 ? "up" : "down")
                }
              >
                {" "}
                <DeriNumberFormat
                  value={trading.tradeInfo.volume24h * 100}
                  decimalScale="2"
                  suffix="%"
                />
              </span>{" "}
              | {trading.symbolInfo.underlying}:{" "}
              <span
                className={
                  trading.tradeInfo.indexPriceVolume24h &&
                  (trading.tradeInfo.indexPriceVolume24h > 0 ? "up" : "down")
                }
              >
                <DeriNumberFormat
                  value={trading.tradeInfo.indexPriceVolume24h * 100}
                  suffix="%"
                  decimalScale="2"
                />
              </span>{" "}
            </>
          ),
          icon: <Icon token="calculator" onClick={() => showSimulatePnl(wallet, trading, lang)} />,
          className: "calculator",
          iconTip: lang["simulate-pnl-icon-tip"],
        },
        {
          ...fileds["total-net-position"],
          value: () => (
            <DeriNumberFormat
              value={trading.tradeInfo.tradersNetVolume}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
        {
          ...fileds["up-depth"],
          value: () => (
            <DeriNumberFormat
              decimalScale="2"
              value={trading.tradeInfo.impactUpNotional}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
      ];
    } else if (Category.isOption) {
      cols = [
        { ...fileds["mark-price"], value: trading.tradeInfo.markPrice },
        {
          ...fileds["dvol"],
          text: trading.symbolInfo.underlier,
          value: () => (
            <>
              <DeriNumberFormat value={trading.tradeInfo.indexPrice} decimalScale={2} /> |{" "}
              {trading.symbolInfo.underlier === "BNBUSD" ? "VOL" : "DVOL"} :{" "}
              <DeriNumberFormat
                value={trading.tradeInfo.volatility * 100}
                decimalScale={2}
                suffix="%"
              />
            </>
          ),
        },
        { ...fileds["payoff"], value: () => <DeriNumberFormat value={trading.tradeInfo.payoff} /> },
        {
          ...fileds["daily-funding"],
          value: () => {
            return (
              <UnderlineText tip={trading.fundingFeeValueTip} multiline>
                <DeriNumberFormat value={trading.tradeInfo.funding0} decimalScale="4" />
              </UnderlineText>
            );
          },
          tip: trading.fundingFeeTip,
        },
        {
          ...fileds["24h-change"],
          value: trading.tradeInfo.volume24h * 100,
          className: trading.tradeInfo.volume24h
            ? trading.tradeInfo.volume24h > 0
              ? "up calculator"
              : "down calculator"
            : "calculator",
          icon: isRetired ? (
            ""
          ) : (
            <Icon token="calculator" onClick={() => showSimulatePnl(wallet, trading, lang)} />
          ),
          iconTip: isRetired ? "" : lang["simulate-pnl-icon-tip"],
        },
        // { ...fileds['24h-change'], value: trading.symbolInfo.volume24h * 100, className: trading.symbolInfo.volume24h && (trading.symbolInfo.volume24h > 0 ? 'up' : 'down') },
        {
          ...fileds["total-net-position"],
          value: () => (
            <DeriNumberFormat
              value={trading.tradeInfo.tradersNetVolume}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
        {
          ...fileds["margin-req"],
          value: () => (
            <>
              <DeriNumberFormat
                value={trading.tradeInfo.initialMarginRatio * 100}
                suffix="%"
                decimalScale="2"
              />{" "}
              |{" "}
              <DeriNumberFormat
                value={trading.tradeInfo.maintenanceMarginRatio * 100}
                suffix="%"
                decimalScale="2"
              />
            </>
          ),
        },
        {
          ...fileds["fee"],
          value: () => (
            <>
              {trading.tradeInfo.feeRatioLabel} *{" "}
              <DeriNumberFormat value={trading.tradeInfo.feeRatio * 100} suffix="%" />
            </>
          ),
        },
        {
          ...fileds["up-depth"],
          value: () => (
            <DeriNumberFormat
              decimalScale="2"
              value={trading.tradeInfo.impactUpNotional}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
      ];
    } else if (Category.isGamma) {
      cols = [
        { ...fileds["index-price"], value: trading.tradeInfo.indexPrice },
        {
          ...fileds["dvol"],
          text: trading.symbolInfo.underlier === "BNBUSD" ? "VOL" : "DVOL",
          value: () => (
            <DeriNumberFormat
              value={trading.tradeInfo.volatility * 100}
              decimalScale={2}
              suffix="%"
            />
          ),
        },
        {
          ...fileds["daily-funding"],
          value: () => {
            return (
              <>
                <UnderlineText tip={trading.fundingFeeValueTip} multiline className="funding-rate">
                  <DeriNumberFormat value={`${trading.tradeInfo.funding0}`} decimalScale="4" />
                </UnderlineText>
                &nbsp;&nbsp;
                <UnderlineText tip={trading.rateTip} multiline className="funding-rate">
                  (
                  <DeriNumberFormat
                    suffix="%"
                    decimalScale="4"
                    value={trading.tradeInfo.fundingFeeRate * 100}
                  />
                  )
                </UnderlineText>
              </>
            );
          },
          tip: trading.fundingFeeTip,
          tipHtml: true,
          element: (
            <span>
              <div>
                {getLangKey("daily-funding-hover-gamma", { symbol: trading.symbolInfo.displaySymbol })}
                <a target="_blank" rel='noreferrer' href='https://im0xalpha.notion.site/Gamma-Swap-edf0ca3382ac46e3bfdbec5001a4d923'> {lang['learn-more']}</a>
              </div>
            </span>
          )
        },
        {
          ...fileds["total-net-position"],
          value: () => (
            <DeriNumberFormat
              value={trading.tradeInfo.tradersNetVolume}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
        // {
        //   ...fileds["margin-req"],
        //   value: () => (
        //     <>
        //       <DeriNumberFormat
        //         value={trading.tradeInfo.initialMarginRatio * 100}
        //         suffix="%"
        //         decimalScale="2"
        //       />{" "}
        //       |{" "}
        //       <DeriNumberFormat
        //         value={trading.tradeInfo.maintenanceMarginRatio * 100}
        //         suffix="%"
        //         decimalScale="2"
        //       />
        //     </>
        //   ),
        // },
        {
          ...fileds["fee"],
          value: () => (
            <>
              <UnderlineText html={true} element={(<div>
                Approximately. Exact transaction fee = {trading.symbolInfo.powerUnderlier}/(1-hT)*{trading.tradeInfo.feeRatio * 100}%
                <a href="https://docs.deri.io/library/academy/gamma-swap/introducing-gamma-swap" target="_blank" rel="noreferrer">
                 {" "} read more
                </a>

              </div>)} tip="fee">
                {trading.symbolInfo.powerUnderlier}
              </UnderlineText>
              &nbsp;<DeriNumberFormat prefix=" * " value={trading.tradeInfo.feeRatio * 100} suffix="%" />
            </>
          ),
        },
        {
          ...fileds["up-depth"],
          value: () => (
            <DeriNumberFormat
              decimalScale="2"
              value={trading.tradeInfo.impactUpNotional}
              suffix={` ${trading.symbolInfo.unit}`}
            />
          ),
        },
      ];
    }
    setColumns(cols);
  }, [trading.tradeInfo, trading.symbolInfo, trading.symbolInfo.volume24h, lang]);
  return (
    <div className="trade-info-content">
      <div className="slide-icon left">
        <Icon token="arrow-left" width="24" height="24" />
      </div>
      <div className="m-body">
        {columns.map((col, index) => (
          <div className="m-col" key={index}>
            <div className="main">
              <div className="m-col-name">
                {col.tip ? (
                  <UnderlineText
                    id={col.id}
                    text={col.text}
                    tip={col.tip}
                    width={col.tipWidth}
                    html={col.tipHtml}
                    element={col.element}
                    multiline
                  />
                ) : (
                  col.text
                )}
              </div>
              <div className={classNames("col-value", col.className)}>
                {typeof col.value === "function" ? (
                  col.value(col)
                ) : (
                  <DeriNumberFormat
                    value={col.value}
                    prefix={col.prefix}
                    suffix={col.suffix}
                    decimalScale={col.decimal || 2}
                  />
                )}
                {col.icon && (
                  <>
                    {col.iconTip && !isMobile() ? (
                      <UnderlineText tip={col.iconTip}>{col.icon}</UnderlineText>
                    ) : (
                      col.icon
                    )}
                  </>
                )}
              </div>
            </div>
            {col.rightSide}
          </div>
        ))}
      </div>
      <div className="slide-icon right">
        <Icon token="arrow-right" width="24" height="24" />
      </div>
    </div>
  );
}
export default inject("trading", "wallet")(observer(TradeInfo));
