import classNames from "classnames";
import Button from "../../../../components/Button/Button";
import Icon from "../../../../components/Icon/Icon";
import UnderlineText from "../../../../components/UnderlineText/UnderlineText";
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import './card.less';
export function Card({ trading, wallet, getLang,onSelect, position, lang, closePosition, twitterShare }) {
  return (
    <div className="position-card">
      <div className="market-close-header">
        <div className="market-name">
          <div className="symbol-name" onClick={() => onSelect(position.displaySymbol, position.b0Unit, position.bTokenLength === 1)}>
            <Icon token={position.underlier} width="16" height="16" />
            <span>{position.displaySymbol}</span>
          </div>
          <div className={classNames("leverage-direction", { "dir-long": position.direction === "Long" })}>
            {position.direction}
          </div>
        </div>
        <div className="close-position-btn">
          <Button
            className="close-btn"
            label={lang["close"]}
            width={48}
            height={24}
            onClick={() => { closePosition(position) }}
          />
        </div>
      </div>
      <div className="position-card-info-box">
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['position']}
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={position.displayVolume} suffix={` ${position.symbolUnit} / `} />
            <DeriNumberFormat value={position.notional} decimalScale={2} suffix={` ${position.displayNotionalUnit} `} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['entry-price']}
          </div>
          <div className="position-card-info-value">
            {position.isGamma ?
              <span>
                <UnderlineText tip={getLang([position.direction === "Long" ? "gamma-enter-price-value-hover" : "gamma-enter-price-value-hover-short"], {
                  unit: position["symbolUnit"], price: position["gammaEntryPrice"] && position["gammaEntryPrice"].toFixed(2)
                })}>
                  <DeriNumberFormat value={position.gammaEntryPrice} decimalScale={2} />
                </UnderlineText>
              </span>
              : <DeriNumberFormat value={position.averageEntryPrice} decimalScale={2} />}

          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            {lang['mark-price']}
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={position.markPrice} decimalScale={2} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            <UnderlineText tip={lang['margin-usage-for-position']}>
              {lang['margin-usage']}
            </UnderlineText>
          </div>
          <div className="position-card-info-value">
            <DeriNumberFormat value={position.marginHeldBySymbol} decimalScale={2} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            <UnderlineText multiline tip={lang['unrealized-pnl-dpmm-hover']}>
              {lang['unrealized-pnl']}
            </UnderlineText>
          </div>
          <div className="position-card-info-value">
            <span className={classNames("pnl-num", {
              "pnl-up": position.unrealizedPnl > 0,
              "pnl-down": position.unrealizedPnl < 0
            })}>
              <DeriNumberFormat value={position.unrealizedPnl} prefix={position.unrealizedPnl > 0 ? "+ " : ""} decimalScale={2} />
            </span>
            <Icon token='scan' width='14' onClick={() => { twitterShare(position) }} />
          </div>
        </div>
        <div className="position-card-info">
          <div className="position-card-info-title">
            <UnderlineText multiline tip={lang['funding-fee-hover']}>
              {lang['fundingFee']}
            </UnderlineText>
          </div>
          <div className="position-card-info-value">
            <span className={classNames("pnl-num", {
              "pnl-up": position.fundingFee.toFixed(2) > 0,
              "pnl-down": position.fundingFee.toFixed(2) < 0
            })}>
              <DeriNumberFormat value={Math.abs(position.fundingFee.toFixed(2))} decimalScale={2} prefix={`${position.fundingFee.toFixed(2) > 0 ? "+" : "-"}`} />
            </span>
          </div>
        </div>
      </div>
    </div>
  )
}