import { useState, useEffect, useRef, useCallback } from "react";
import Input from "../../../components/Input/Input";
import styled from "styled-components";
import Slider from "../../../components/Slider/Slider";
import ButtonGroup from "../../../components/ButtonGroup/ButtonGroup";
import Button from "../../../components/Button/Button";
import Checkbox from "../../../components/Checkbox/Checkbox";
import {
  BUY,
  SELL,
  TRADE_ENTRY_DOLLAR_STATUS,
  TRADE_ENTRY_VOLUME_STATUS,
  TRADING_CLOSE_POSITION_STATUS,
  SKIP_TRADE_CONFIRMATION,
  TRADE_ENTRY_PERCENTAGE_STATUS,
  TRADE_ENTRY_SLIDER_STATUS,
  MODAL_OPTIONS,
  GAMMA,
} from "../../../utils/Constants";
import "./leverageBar.less";
import classNames from "classnames";
import { show } from "react-functional-modal";
import Confirmation from "./Confirmation";
import { inject, observer } from "mobx-react";
import Toggle from "../../../components/Toggle/Toggle";
import Intl from "../../../model/Intl";
import { useAlert } from "react-alert";
import {
  isMobile,
  storeSkipConfirmation,
  isElementInViewport,
  isScrollBottom,
  bg,
  numberWithCommas,
  buried,
} from "../../../utils/utils";
import DeriNumberFormat from "../../../utils/DeriNumberFormat";
import TradeWarnning from "./TradeWarnning";
import AdvancedSetting from "./AdvancedSetting/AdvancedSetting";
import Category from "../../../model/Category";
import Icon from "../../../components/Icon/Icon";
const getLang = (key, params, options) => {
  return Intl.eval("trade", key, params, options);
};

function LeverageBar({ trading, wallet, lang, direction = BUY, type }) {
  const [skipConfirmation, setSkipConfirmation] = useState(false);
  const [isActionSticky, setIsActionSticky] = useState(true);
  const [gammaAdvancedOpen, setGammaAdvancedOpen] = useState(false);
  const [placeholderPrice, setPlaceholderPrice] = useState(`Current ${trading.symbolInfo.gammaUnderlier} Price`)
  const alert = useAlert();
  const inputTipRef = useRef();
  const btnRef = useRef();
  const buttonText = direction === BUY ? lang["buy-long"] : lang["sell-short"];
  const clazz = classNames("leverage-bar", direction, {
    gamma: Category.isGamma,
  });
  const closeOnlyClazz = classNames("close-only", {
    show:
      (direction === BUY && +trading.position.volume < 0) ||
      (direction === SELL && +trading.position.volume > 0),
  });
  const transSummaryClazz = classNames("trans-summary", {
    show: trading.volume > 0,
  });
  const inputTipClass = classNames("input-tip", {
    show: trading.volume > 0,
  });
  const orderSliderBarClass = classNames("order-slider", {
    "close-only": trading.closeOnly,
    "close-position": trading.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS,
  });
  const tradeWarnClass = classNames({
    show: trading.tradeWarn,
  });
  const parsedVolumeClass = classNames("parsed-volume", {
    show: trading.hasFormatUnit && trading.displayVolume > 0,
  });
  const actionClass = classNames("action", {
    sticky: isActionSticky,
  });

  const gammaAdvancedClass = classNames("customize", {
    show: gammaAdvancedOpen,
    hide: !gammaAdvancedOpen,
  });

  const currentIndexClass = classNames("left", {
    hide: bg(trading.gammaEntryPrice).gt(0),
  });

  const handleFocus = () => {
    setPlaceholderPrice("")
  }
  const handleBlur = () => {
    setPlaceholderPrice(`Current ${trading.symbolInfo.gammaUnderlier} Price`)
  }

  const doTransaction = async () => {
    buried("buy/sell", "trade_status", direction);
    if (skipConfirmation) {
      const res = await trading.doTransaction();
      if (res && res.success) {
        storeSkipConfirmation(1);
        trading.refresh();
      }
    } else {
      show(
        <Confirmation
          clear={clear}
          subject={`${buttonText} ${lang["order"]}`}
          lang={lang}
          direction={direction}
          wallet={wallet}
          type={type.current}
          trading={trading}
          alert={alert}
          skipConfirmation={skipConfirmation}
        />,
        { ...MODAL_OPTIONS }
      );
    }
  };

  const onSlide = (value) => {
    trading.setEntryStatus(TRADE_ENTRY_SLIDER_STATUS);
    trading.setInputPercentageValue("");
    trading.setInputSliderValue(value);
  };

  const percentSelect = (value) => {
    trading.setEntryStatus(TRADE_ENTRY_PERCENTAGE_STATUS);
    trading.setInputPercentageValue(value);
  };
  const inputVolume = (volume) => {
    trading.setEntryStatus(TRADE_ENTRY_VOLUME_STATUS);
    trading.setInputPercentageValue("");
    trading.setVolume(volume);
  };
  const dollarOnChange = (dollar) => {
    trading.setEntryStatus(TRADE_ENTRY_DOLLAR_STATUS);
    trading.setInputPercentageValue("");
    trading.setInputDollarValue(dollar);
  };

  const closeOnly = (checked) => {
    trading.setCloseOnly(checked);
    trading.setInputPercentageValue("");
  };

  const clear = () => {
    trading.setInputPercentageValue("");
    trading.setInputDollarValue("");
    trading.setInputSliderValue("");
    trading.setVolume("");
    trading.setGammaEntryPrice('')
    trading.setCloseOnly(false);
  };

  const onSlippageChange = (value) => {
    const slippage = bg(value).div(100).toNumber();
    trading.setSlippage(slippage);
  };

  const onSkipConfirmationChange = (checked) => {
    setSkipConfirmation(checked);
  };

  const onGammaAdvancedSettingChanged = useCallback(() => {
    setGammaAdvancedOpen(!gammaAdvancedOpen);
  }, [gammaAdvancedOpen]);

  const onGammaEntrypriceChanged = useCallback((value) => {
    if (+value < 0) {
      trading.setGammaEntryPrice("")
    }
    trading.setGammaEntryPrice(value);
  }, []);

  useEffect(() => {
    clear();
    trading.setDirection(direction);
  }, [direction, trading.symbolInfo.symbol]);

  useEffect(() => {
    setSkipConfirmation(trading.skipTradeConfirmation);
  }, []);

  const handler = () => {
    const inputTipVisible = isElementInViewport(inputTipRef.current);
    if (inputTipVisible && trading.volume && +trading.volume > 0) {
      setIsActionSticky(true);
    } else {
      setIsActionSticky(false);
    }
  };

  useEffect(() => {
    handler();
    document.addEventListener("scroll", handler, false);
    document.addEventListener("resize", handler, false);
    if (Category.isGamma && trading.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS) {
      trading.setCloseOnly(true)
    }
    return () => {
      document.removeEventListener("scroll", handler);
      document.removeEventListener("resize", handler);
    };
  }, [trading.volume, trading.closeOnly, trading.positionTradingStatus]);

  return (
    <div className={clazz}>
      <div className="tip">{lang["set-volume"]}</div>
      <div className="input-area">
        <Input
          value={trading.displayVolume}
          minTradeVolume={trading.minEntryVolume}
          step={trading.minEntryVolume}
          max={trading.maxEntryVolume}
          onChange={inputVolume}
          decimal={trading.minTradeScaleDecimal}
          unit={trading.curTradeUnit}
          unitTip={
            trading.hasFormatUnit
              ? `1 ${trading.curTradeUnit} = ${numberWithCommas(trading.contract.multiplier)} ${trading.symbolInfo.unit
              }`
              : ""
          }
          className="l-input"
        />
        <div className="cutting-line"></div>
        <Input
          value={trading.dollarValue}
          unit={
            trading.symbolInfo.singleBtoken ? trading.symbolInfo.bTokenSymbol.toUpperCase() : "USD"
          }
          step="0.01"
          min={0}
          max={trading.maxDollarValue}
          onChange={dollarOnChange}
          className="l-input"
          inputWidth="70%"
        />
      </div>
      <div className={parsedVolumeClass}>
        ={" "}
        <DeriNumberFormat
          value={trading.realTradeVolume}
          decimalScale={trading.minTradeScaleDecimal}
          suffix={` ${trading.symbolInfo.unit}`}
        />
      </div>
      <div className="gamma-special">
        <div className="default">
          <span>
            {lang["entry-price"]} : {(direction === BUY && +trading.position.volume < 0) ||
              (direction === SELL && +trading.position.volume > 0) ? "Not Applicable" : trading.gammaEntryPrice ? trading.gammaEntryPrice : `Current ${trading.symbolInfo.gammaUnderlier} Price`}
          </span>
          <span>
            <Icon
              token={gammaAdvancedOpen ? 'arrow-up' : 'arrow-down'}
              width="20"
              height="20"
              className="arrow-icon"
              onClick={onGammaAdvancedSettingChanged}
            />
          </span>
        </div>
        <div className={gammaAdvancedClass}>
          {/* <div className={currentIndexClass} onClick={() => onGammaEntrypriceChanged("")}>
            <div>
              {lang["current-price"]}
              Current Index
            </div>
            <Icon token="selected" className="selected-icon" width="12" height="9" />
          </div> */}
          <div className="right">
            <Input
              value={trading.gammaEntryPrice}
              onChange={onGammaEntrypriceChanged}
              placeholder={trading.closeOnly ? "Not Applicable" : `Current ${trading.symbolInfo.gammaUnderlier} Price`}
              disabled={trading.closeOnly}
              step="0.01"
              className="entry-price-input"
              inputWidth="48%"
            // onFocus={handleFocus}
            // onBlur={handleBlur}
            />
          </div>
        </div>
      </div>
      <div className="slider-bar">
        <Slider
          max={trading.maxAvailableMargin}
          trackStyle={trading.sliderStyle.trackStyle}
          railStyle={trading.sliderStyle.railStyle}
          handleStyle={trading.sliderStyle.handleStyle}
          onSlide={onSlide}
          value={trading.sliderValue}
          step={trading.contract.multiplier > 1 ? 1 : trading.contract.multiplier}
          className={orderSliderBarClass}
        />
      </div>
      <div className="quick-input">
        <ButtonGroup
          outline={true}
          onChange={percentSelect}
          defaultValue={trading.inputPercentageValue}
          className="l-button-group"
        />
        <div className="input-helper">
          <div className={closeOnlyClazz}>
            <Toggle
              label={lang["close-only"]}
              className="l-toggle"
              tip={trading.symbolInfo.isGamma && trading.closeOnly ? `Over ${direction === BUY ? "longing" : "shorting"} is not allowed` : null}
              onClick={closeOnly}
              checked={trading.closeOnly}
            />
          </div>
          <div className="clear" onClick={clear}>
            {lang["clear"]}
          </div>
        </div>
        <div className={inputTipClass} ref={inputTipRef}>
          <span className="number"> {trading.inputHelpText}</span>
        </div>
      </div>
      <TradeWarnning
        content={trading.isRetired ? lang["leveragebar-retired-warn"] : lang["warn-text"]}
        className={tradeWarnClass}
      />
      <div className={transSummaryClazz}>
        <div className="trans-title">{lang["transaction-summary"]}</div>
        <div className="trans-body">
          <div className="trans-row">
            <div className="trans-col">{lang["margin-usage"]}</div>
            <div className="trans-col">
              <DeriNumberFormat
                value={trading.impactPreview.marginUsage}
                allowZero={true}
                decimalScale={2}
              />
              &nbsp;{"->"}&nbsp;{" "}
              <DeriNumberFormat
                value={trading.impactPreview.marginUsageAfter}
                allowZero
                decimalScale={2}
              />
            </div>
          </div>
          {!Category.isGamma && (
            <div className="trans-row">
              <div className="trans-col">{lang["trade-price"]}</div>
              <div className="trans-col">
                <DeriNumberFormat
                  value={trading.impactPreview.tradePrice}
                  decimalScale={trading.symbolInfo.decimals || 2}
                />
              </div>
            </div>
          )}
          <div className="trans-row">
            <div className="trans-col">{lang["fee"]}</div>
            <div className="trans-col">
              <DeriNumberFormat value={trading.impactPreview.fee} decimalScale={2} />
            </div>
          </div>
          {Category.isFutures && (
            <div className="trans-row">
              <div className="trans-col">{lang["liquidate-price"]}</div>
              <div className="trans-col">
                <DeriNumberFormat value={trading.impactPreview.liqPriceAfter} decimalScale={2} />
              </div>
            </div>
          )}
        </div>
      </div>
      <AdvancedSetting
        lang={lang}
        onSlippageChange={onSlippageChange}
        onSkipConfirmationChange={onSkipConfirmationChange}
        skipConfirmation={skipConfirmation}
      />
      <div className={actionClass} ref={btnRef}>
        <Button
          trading={trading}
          wallet={wallet}
          disabled={!trading.isTradingReady}
          lang={lang}
          label={trading.openInterst ? "Exceed Open Interest" : buttonText}
          className="l-button"
          onClick={doTransaction}
        />
      </div>
    </div>
  );
}
export default inject("trading", "type", "wallet")(observer(LeverageBar));
