import { useEffect, useState } from 'react'
import { components } from '@reactour/tour'
import { show, hide } from 'react-functional-modal'
import RiskWarning from '../RiskWarning/RiskWarning'
import MarginPanel from '../MarginPanel/MarginPanel'
import { setCookie, isMobile, getCookie } from '../../utils/utils'
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock'
import { TourProvider, useTour } from '@reactour/tour'
import './tradeGuide.less'
import closeModal from '../../assets/img/close-modal-icon.svg'
const steps = [
  {
    selector: '.wallet-view',
    content: () => (
      <div className='step-one'>
        Connect your wallet
      </div>
    ),
  },
  {
    selector: '.deposit-btn',
    content: () => (
      <div className='step-one'>
        Deposit margin
      </div>
    ),
  },
  {
    selector: '.b-token-list',
    content: () => (
      <div className='step-one'>
        Select the token you’d like to use as margin
      </div>
    ),
  },
  {
    selector: '.mp-main',
    content: () => (
      <div className='step-one'>
        Enter the deposit amount
      </div>
    )

  },
  {
    selector: '.leverage-bar',
    content: () => (
      <div className='step-one'>
        Enter your order amount
      </div>
    ),
  },
  {
    selector: '.action',
    content: () => (
      <div className='step-one'>
        Place your order
      </div>
    )
  },
  {
    selector: '.my-info',
    content: () => (
      <div className='step-one'>
        View all your positions
      </div>
    )
  }

]



export default function TradeGuide({ trading, wallet, lang, getLang }) {
  const { setIsOpen } = useTour()
  const [isNewAccount, setIsNewAccount] = useState(false);
  const back = () => {
    setIsNewAccount(true)
  }
  useEffect(() => {
    let interval = null;
    interval = window.setTimeout(() => {
      if (wallet.status === 'disconnected') {
        let cookie = getCookie('newAccount')
        if (cookie !== 'true') {
          show(<RiskWarning back={back} />, {
            key: 'RiskWarning',
            fading: true,
            style: {
              background: "rgba(0, 0, 0, 0.4)",
              zIndex: 99999,
            }
          })

        }
      }
    }, 5000)
    return () => clearTimeout(interval)

  }, [wallet, wallet.detail, wallet.detail.account])

  const showMarginPanel = () => {
    show(<MarginPanel type='deposit' getLang={getLang} close={() => hide('marginPanel')} trading={trading} wallet={wallet} lang={lang} pool={trading.pool} alert={alert} />, {
      key: 'marginPanel',
      fading: true,
      style: {
        background: "rgba(0, 0, 0, 0)",
        zIndex: 2,
      }
    })
  }
  const Badge = ({ children }) => {
    return (
      <div className='step-num'><span className='now-step'>{children}</span>  / 7</div>
    )
  }
  const Close = ({ onClick }) => {
    const close = () => {
      onClick()
      setCookie('newAccount', "true")
      trading.setPositions([])
    }
    return (
      <img onClick={close} alt='' className='close-modal' src={closeModal}></img>
    )
  }

  const nextButton = ({ currentStep, stepsLength, setIsOpen, setCurrentStep, }) => {
    const last = currentStep === stepsLength - 1
    return (
      <button className='next' onClick={() => {
        if (last) {
          setIsOpen(false)
          setCookie('newAccount', "true")
          trading.setPositions([])
        } else {
          if (currentStep === 1) {
            showMarginPanel()
          } else if (currentStep === 3) {
            hide('marginPanel')
          }
          if (currentStep === 5) {
            trading.positions = [{
              "category": "futures",
              "symbol": "BTCUSD",
              "displaySymbol": "BTCUSD",
              "symbolUnit": "BTC",
              "b0Unit": "BUSD",
              "bTokenLength": 18,
              "poolInitialMarginMultiplier": "10",
              "price": "16806.507",
              "markPrice": "16812.876722757842056638",
              "displayMarkPrice": "16812.876722757842056638",
              "theoreticalPrice": "",
              "volume": "0.0001",
              "displayVolume": "0.0001",
              "cost": "1.681287400069376223",
              "notional": "1.6812876722757842056638",
              "averageEntryPrice": "16812.87400069376223",
              "displayAverageEntryPrice": "16812.87400069376223",
              "margin": "4.891270986179507624",
              "marginHeldTotal": "0.067226028",
              "marginHeldBySymbol": "0.067226028",
              "unrealizedPnl": "0",
              "unrealizedPnlTotal": "-0.000636700069376223",
              "fundingFee": 0,
              "fundingFeeTotal": "0.0000001179578288489269",
              "liquidationPrice": {
                "numPositions": 0,
                "price1": null,
                "price2": null,
                "underlier": "BTCUSD"
              },
              "balanceContract": "4.891270986179507624",
              "direction": "Long",
              "formatedSymbol": "BTCUSD",
              "directionColor": "green",
              "unrealizedPnlColor": "red",
              "fundingFeeColor": "red",
              "notionalColor": "gray",
              "displayNotionalUnit": "USD",
              "unrealizedPnlRate": "0",
              "unrealizedPnlRateColor": "gray",
              "isOptions": false,
              "underlier": "BTCUSD"
            }]
          }
          setCurrentStep(s => (s === steps.length - 1 ? 0 : s + 1))

        }
      }}>
        {last ? "DONE" : "NEXT"}
      </button>
    )
  }

  const prevButton = ({ currentStep, stepsLength, setIsOpen, setCurrentStep, }) => {
    const fist = currentStep === 0
    return (
      <>
        {
          !fist ? <button className='prev' onClick={
            () => {
              if (currentStep === 4 || currentStep === 3) {
                showMarginPanel()
              } else {
                hide('marginPanel')
              }
              setCurrentStep(s => (s === 1 ? 0 : s - 1))

            }}>
            PREV
          </button> : null}

      </>

    )
  }


  const disPlayBody = target => disableBodyScroll(target)
  const enableBody = target => enableBodyScroll(target)
  return (
    <div>
      {isNewAccount && <TourProvider
        steps={steps}
        defaultOpen={isNewAccount}
        components={{ Close, Badge }}
        className='trade-guide'
        disableFocusLock={false}
        disableInteraction={true}
        onClickMask={() => { setIsOpen(true) }}
        afterOpen={disPlayBody}
        beforeClose={enableBody}
        scrollSmooth
        nextButton={nextButton}
        prevButton={prevButton}
      />}
    </div>
  )
}