import { useState, useEffect } from "react";
import { AreaChart, Area, XAxis, YAxis, Dot, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import Tip from './Tip'
import { bg } from "../../utils/utils";
export default function Chart({ arr, underlier, isLong, onChange, lastData }) {
  const [off, setOff] = useState(0);
  const [data, setData] = useState([])
  const [areaKey, setAreaKey] = useState(0)
  const gradientOffset = () => {
    const dataMax = Math.max(...data.map((i) => i.pnl));
    const dataMin = Math.min(...data.map((i) => i.pnl));
    if (dataMax <= 0) {
      setOff(0)
      return 0;
    }
    if (dataMin >= 0) {
      setOff(1)
      return 1;
    }
    setOff(dataMax / (dataMax - dataMin))
  };

  const getKey = () => {
    let keys = Math.random() * 100
    setAreaKey(keys)
  }

  const CustomizedDot = (props) => {
    const { cx, cy, stroke, strokeWidth, points, payload } = props;
    if (points.length > 0 && payload.curIndexPrice) {
      if (lastData && bg(lastData.curIndexPrice).eq(payload.curIndexPrice)) {
        let color = "#38C7A4"
        if (stroke === "#CE5E5E") {
          color = "#D44242"
        }
        return (
          <Dot cx={cx} cy={cy} r={4} stroke="white" strokeWidth={strokeWidth} fill={color} />
        );
      } else if (bg(payload.curIndexPrice).eq(payload.entryPrice)) {
        return (
          <Dot cx={cx} cy={cy} r={4} stroke="white" strokeWidth={strokeWidth} fill="#5577FD" />
        );
      } else {
        return null
      }
    } else {
      return null
    }
  };
  const formatXAxis = (tickItem) => {
    return parseInt(tickItem);
  };
  useEffect(() => {
    setData(arr)
    getKey()
  }, [arr])
  useEffect(() => {
    if (data) {
      gradientOffset()
    }
  }, [data])
  return (data ?
    <ResponsiveContainer>
      <AreaChart data={data} >
        <CartesianGrid vertical={true} horizontal={false} stroke="#203B60" />
        <XAxis dataKey="curIndexPrice" tickFormatter={formatXAxis} axisLine={false} hide={false} tickLine={false} allowDataOverflow={false}
          tick={{ fill: '#fff', fontSize: '14' }} />
        <YAxis dataKey="pnl" tickLine={false} axisLine={false} hide={false} tick={{ fill: '#fff', fontSize: '14' }} />
        <Tooltip itemStyle={{ stroke: "#5577FD" }} cursor={{ stroke: '#5577FD' }} active={true} content={<Tip underlier={underlier} onChange={onChange} lastData={lastData} isLong={isLong} />} />
        <defs>
          <linearGradient id="splitColor" x1="0" y1="0" x2="0" y2="1">
            <stop offset={off} stopColor="#59AE99" stopOpacity={0.5} />
            <stop offset={off} stopColor="#CE5E5E" stopOpacity={0.5} />
          </linearGradient>
        </defs>
        <Area type="monotone" key={areaKey} dot={<CustomizedDot />} dataKey="pnl" stroke={!isLong ? "#CE5E5E" : "#59AE99"} fillOpacity={0.5} strokeWidth={2} fill="url(#splitColor)" />
      </AreaChart>
    </ResponsiveContainer>
    : null)
}